/* mint-uiprimaryUI */
/*author ypf date 2018-11-30*/
* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  margin: 0px;
  padding: 0px;
}
body {
  background: #eeeeee;
  font-size: 0.7rem;
  font-family: "PingFangSC-Regular", sans-serif;
}
a {
  text-decoration: none;
  color: #111111;
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
li {
  list-style: none;
}
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
.clearfix {
  *zoom: 1;
}
.bc-content {
  padding-top: 2.7rem;
}
em.ec-checkbox {
  display: inline-block;
  width: 0.5rem;
  height: 0.5rem;
}
em.ec-checkbox.checked {
  background: url("../assets/images/checkbox-checked.png") no-repeat center;
  background-size: cover;
}
em.ec-checkbox.unchecked {
  background: url("../assets/images/checkbox-un.png") no-repeat center;
  background-size: cover;
}
.form-item {
  display: flex;
  align-items: center;
  width: 100%;
  height: 1.85rem;
  margin: 0 0 0.9rem 0;
  overflow: hidden;
}
.form-item.ec-border {
  border: 1px solid #979797;
}
.form-item input {
  margin-left: 1px;
  border: none;
  background: none;
}
.form-item input:focus {
  outline: none;
}
.ec-submit {
  width: 100%;
  height: 1.85rem;
  line-height: 1.85rem;
  background: #ff3e3e;
  color: #fff;
  text-align: center;
  margin-bottom: 0.4rem;
}
.illustrate-wapper {
  display: flex;
  align-items: center;
}
.illustrate-wapper .agree-wapper {
  margin-left: 0.8rem;
  color: #4d4c4d;
  font-size: 0.6rem;
}
.illustrate-wapper .agree-wapper .agree {
  color: #ff3e3e;
  font-size: 0.6rem;
}
.mint-header {
  background-color: #FF3E3E;
}
.mint-button:not(.is-disabled):active::after {
  opacity: 0.2 /* .6 */;
}
.mint-button--primary {
  background-color: #FF3E3E;
}
.mint-button--primary.is-plain {
  border: 1px solid #FF3E3E;
  color: #FF3E3E;
}
.mint-badge.is-primary {
  background-color: #FF3E3E;
}
.mint-switch-input:checked + .mint-switch-core {
  border-color: #FF3E3E;
  background-color: #FF3E3E;
}
.mint-navbar .mint-tab-item.is-selected {
  border-bottom: 3px solid #FF3E3E;
  color: #FF3E3E;
}
.mint-tabbar > .mint-tab-item.is-selected {
  color: #FF3E3E;
}
.mint-searchbar-cancel {
  color: #FF3E3E;
}
.mint-checkbox-input:checked + .mint-checkbox-core {
  background-color: #FF3E3E;
  border-color: #FF3E3E;
}
.mint-radio-input:checked + .mint-radio-core {
  background-color: #FF3E3E;
  border-color: #FF3E3E;
}
.mt-range-progress {
  background-color: #FF3E3E;
}
.mt-progress-progress {
  background-color: #FF3E3E;
}
.mint-msgbox-confirm {
  color: #FF3E3E;
}
.mint-msgbox-confirm:active {
  color: #FF3E3E;
}
.mint-datetime-action {
  color: #FF3E3E;
}



