//
em.ec-checkbox {
  display: inline-block;
  width: 0.5rem;
  height: 0.5rem;
  &.checked {
    background: url("../assets/images/checkbox-checked.png") no-repeat
      center;
    background-size: cover;
  }
  &.unchecked {
    background: url("../assets/images/checkbox-un.png") no-repeat center;
    background-size: cover;
  }
}


//
.form-item {
  display: flex;
  align-items: center;
  width: 100%;
  height: 1.85rem;
  margin: 0 0 0.9rem 0;
  overflow: hidden;
  &.ec-border {
    border: 1px solid #979797;
  }
  input{
    margin-left: 1px;
    border: none;
    background: none;
    &:focus {
      outline: none;
    }
  }
}


.ec-submit {
  width: 100%;
  height: 1.85rem;
  line-height: 1.85rem;
  background: #ff3e3e;
  color: #fff;
  text-align: center;
  margin-bottom: 0.4rem;
}


.illustrate-wapper {
  display: flex;
  align-items: center;
  .agree-wapper {
    margin-left: 0.8rem;
    color: #4d4c4d;
    font-size: 0.6rem;
    .agree {
      color: #ff3e3e;
      font-size: 0.6rem;
    }
  }
}


